<template>
  <div class="col-md-6 mb-3 biodata">
    <div class="card card-body ">
      <div class="text-center mt-1 mb-3">
        <img src="http://wandriputra.github.io/images/pic/bajo.jpg" alt="" width="200px" class="img-center center-block">
      </div>
      <div class="col-md-12">
        <div class="row">
          <div class="col-md-12">
            Nama : {{biodata.account.firstName}} {{biodata.account.lastName}} 
          </div>
          <div class="col-md-6">
            NIP : {{biodata.cardNumber}}
          </div>
          <div class="col-md-6">
            Jurusan : {{biodata.majorField.name}}
          </div>
          <div class="col-md-6">
            Tahun Angkatan :
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
  import ApiEndpoint from '@/helpers/Endpoint';
  import Adapter from '@/helpers/Adapter';

  export default {
    data() {
      return {
        biodata: null,
        accountId: null,
        errors: null,
      };
    },
    methods: {
      getBiodata() {
        this.accountId = Adapter.getAccountId();
        const url = '/member/'+this.accountId;
        ApiEndpoint.getEndpoint(url, Adapter.getToken())
          .then((response) => {
            this.biodata = response.data.data;
          })
          .catch((e) => {
            this.errors = e;
          });
      },
    },
    created() {
      this.getBiodata();
    },
  };
</script>


