<template>
  <div>
    <Header :menus="menus"></Header>
    <div class="container-fluid">
      <div class="row">
        <Sidebar :menus="menus"></Sidebar>
        <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-4">
          <router-view></router-view>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
  import Header from '../_layout/headerAdmin';
  import Sidebar from '../_layout/sideBarAdmin';

  export default {
    components: {
      Header,
      Sidebar,
    },
    data() {
      return {
        menus: [
          { name: 'Dashboard', url: '/admin/' },
          { name: 'List Paket Soal', url: '/admin/list-package' },
          { name: 'List Soal', url: '/admin/list-question' },
          { name: 'Verifikasi Pembayaran', url: '/admin/list-payment' },
          { name: 'Akun Mahasiswa', url: '/admin/account-member' },
          { name: 'Akun Dosen', url: '/admin/account-dosen' },
          { name: 'Akun Reviewer', url: '/admin/account-reviewer' },
          { name: 'Akun Admin', url: '/admin/account-admin' },
          { name: 'Buat Jadwal', url: '/admin/create-schedule' },
          { name: 'List Jadwal', url: '/admin/list-schedule' },
          { name: 'Status Ujian', url: '/admin/exam-status' },
          { name: 'Pengumuman', url: '/admin/announcement' },
        ],
      };
    },
  };
</script>

<style scoped>
body {
  font-size: .875rem;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}
/*
 * Utilities
 */

.border-top { border-top: 1px solid #e5e5e5; }
.border-bottom { border-bottom: 1px solid #e5e5e5; }
</style>


