<template>
    <header>
        <transition mode="out-in">
            <div v-if="show" class="balck bg-mhs " id="navbarHeader">
                <div class="container">
                    <div class="row">
                        <div class="col-sm-12 py-4 nav nav-masthead justify-content-center">
                            <router-link :to="menu.url" class="nav-link" v-for="menu in menus" :key="menu.id">{{menu.name}}</router-link>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <div class="navbar navbar-dark bg-mhs box-shadow">
            <div class="container d-flex justify-content-between">
                <a href="/" class="navbar-brand d-flex align-items-center">
                    <img src="../../assets/ukom-logo-white.png" alt="">
                </a>
                
                <button 
                        @click="show = !show"
                        class="navbar-toggler" 
                        type="button"
                        data-toggle="collapse" 
                        data-target="#navbarHeader" 
                        aria-controls="navbarHeader" 
                        aria-expanded="false" 
                        aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="pull-rigth menu-main nav nav-masthead justify-content-center">
                    <router-link :to="menu.url" class="nav-link" v-for="menu in menus" :key="menu.id">{{menu.name}}</router-link>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
export default {
  props: {
    menus: {
      type: Array,
      required: true,
      default: [
          { name: 'Beranda', url: '/' },
          { name: 'Daftar', url: '/register' },
          { name: 'Masuk', url: '/login' },
          { name: 'Kontak', url: '/contact' },
      ],
    },
  },
  data() {
    return {
      show: false,
    };
  },
};
</script>


<style lang="scss" scoped> 
</style>


