<template>
    <div class="question">
        <p class="question-number">Soal {{ quis[0].number }}</p>
        <hr>
        <p>{{ quis[0].question }}</p>
        <div class="options">
            <p @click="selectAnswer(answer.opsi)" :class="[answer.opsi, selectedOpsi ? 'selected' : '']" v-for="answer in quis[0].options" :key="answer.opsi">
                <span class="radio-option">{{answer.opsi}}</span>
                <span class="text-option">{{answer.text}}</span>
            </p>
        </div>
    </div>
</template>

<script>
/* eslint no-console: ["error", { allow: ["log"] }] */
export default {
  props: {
    quis: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedOpsi: false,
    };
  },
  methods: {
    selectAnswer(answer = null) {
      if (answer != null) {
        this.selectedOpsi = true;
        // console.log(e);
        return this.selectedOpsi;
      }
      return null;
    },
  },
};
</script>

<style scoped>

</style>


