// guest
/* eslint-disable */ 
import Auth from '@/helpers/Middleware';
import GuestMaster from '@/components/Guest/MasterGuest';
import GuestHome from '@/components/Guest/Index';
import GuestRegister from '@/components/Guest/Register';
import GuestLogin from '@/components/Guest/Login';
import GuestContact from '@/components/Guest/Contact';

export default {
  path: '/',
  name: 'Guest',
  component: GuestMaster,
  children: [
    {
      path: '/',
      component: GuestHome,
    },
    {
      path: '/register',
      component: GuestRegister,
    },
    {
      path: '/login',
      component: GuestLogin,
    },
    {
      path: '/contact',
      component: GuestContact,
    },
  ],
};



