<template>
  <div class="col-md-6 mb-3">
    <div class="card card-body">
      <a :href="link.url" class="">{{link.name}}</a>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  props: {
    link: {
      type: Object,
      required: true,
      default: { name: 'Beranda', url: '/' },
    },
  },
};
</script>



