<template>
  <div>
<form class="form-signin">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">Login</h1>
        <p></p>
      </div>

      <div class="form-label-group">
        <input type="email" id="inputEmail" class="form-control" placeholder="Email address" v-model="cridential.email" required autofocus>
        <label for="inputEmail">Email address</label>
        <div v-if="errors.email" class="error-validation">{{errors.email[0]}}</div>
      </div>

      <div class="form-label-group">
        <input type="password" id="inputPassword" class="form-control" placeholder="Password" v-model="cridential.password" required>
        <label for="inputPassword">Password</label>
      </div>

      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" @click="postLogin" type="submit">Masuk</button>
      <p class="mt-5 mb-3 text-muted text-center">&copy; 2017-2018</p>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
  import Adapter from '@/helpers/Adapter';
  import ApiEndpoint from '@/helpers/Endpoint';

  export default {
    data() {
      return {
        cridential: {
          email: '',
          password: '',
        },
        status: '',
        errors: '',
      };
    },
    methods: {
      postLogin(e) {
        e.preventDefault();
        ApiEndpoint.postEndpoint('/login', this.cridential)
        .then((respone) => {
          if (respone.data.data.api_token !== '') {
            this.setData(respone.data.data);
            switch (respone.data.data.role) {
              case 'member':
                Router.push('/mhs');
                break;
              case 'administration':
                Router.push('/admin');
                break;
              case 'reviewer':
                Router.push('/reviewwer');
                break;
              case 'lecture':
                Router.push('/dosen');
                break;
              default:
                break;
            }
          }
        }).catch((err) => {
          this.errors = err.response.data.errors;
        });;
        console.log(this.errors);
      },
    },
  };
</script>

<style scoped>

</style>


