<template>
    <div class="content">
        <Timer :server="123123"></Timer>
        <div class="content-box content-exam" :class="{'is-waiting': loader}">
            <Quest :quis="quis"></Quest>
            <p v-if="quis == ''" class="kosong">Loading...</p>
            <Navigation :next="quis[0].next" :prev="quis[0].prev"></Navigation>
            <!-- {{$route.query.kode_ujian}} {{number}} -->
        </div>
        
        <div class="dot-pages">
            <div class="numberCircle dot" v-on:click="changeQuestion(i)" v-for='i in 100' :key="i.id">
              <span v-if="i == quis[0].number" class="selected">{{i}}</span>
              <span v-else>{{i}}</span>
            </div>
        </div>
    </div>
</template>

<script>
import Timer from './Timer';
import Quest from './Question';
import Navigation from './Navigation';

// import { getQuestion } from '../../../../utils/exam-simple-api';

export default {
  components: {
    Quest,
    Timer,
    Navigation,
  },
  data() {
    return {
      quis: '',
      loader: true,
      number: 16,
    };
  },
  created() {
    this.number = this.$route.params.number;
    this.$soalBus.$on('questionNumber', this.changeQuestion);
  },
  methods: {
    // getQuestions(examQuestion) {
    //   this.loader = true;
    //   getQuestion(examQuestion).then((q) => {
    //     this.quis = q;
    //     this.loader = false;
    //   });
    // },
    // changeQuestion(number) {
    //   this.number = number;
    //   this.getQuestions(this.number);
    // },
  },
  mounted() {
    // this.getQuestions(this.number);
  },
};
</script>

<style lang="scss" scope>
    .kosong{
        text-align: center;
        font-size: 1.5em;
        padding-top: 10px;
    }

    .dot > .selected{
      background-color: #8dc63f;
      color: #F9F9F9;
      border-radius: 50%;
      width: 18px;
      height: 18px;
      padding: 8px;
      border: 2px solid #F9F9F9;
      text-align: center;
      font: 16px Arial, sans-serif;
      margin-left: -10px;
    }

    .dot-pages{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        padding-bottom: 50px;
        padding-top: 50px;
        align-content: center;
    }

    .numberCircle {
        border-radius: 50%;
        width: 16px;
        height: 16px;
        padding: 4px;
        border: 2px solid #666;
        color: #666;
        text-align: center;
        font: 12px Arial, sans-serif;
        margin: 15px 10px;
    }

    .content-exam{
        margin-top: 3.5em !important;
    }

    .navigation{
        padding: 50px 15px;
       
    }

    .question{
        width: 640px;
        margin: auto;
        padding: auto;
        line-height: 2em;
        letter-spacing: .3px;
    }
    .question-number{
        padding: 15px 0px 0px 0px;
        font-size: 1.5em;
    }
    .options{
        padding-top: 10px;
        >p{
            padding: 5px 0px;
            display: flex;
        }
    }
    .radio-option{
        height: 25px;
        width: 30px;
        background-color: rgb(241, 241, 241);
        box-shadow: 1px 1px 5px #cececea1;
        border-radius: 10px;
        text-align: center;
        :hover{
            background-color: #F6C75D;
        }
    }
    .text-option{
        width: 100%;
        padding-left: 10px;
    }

    .is-waiting {
        position: relative;
        transition-duration: .3s;
        > * {
            opacity: .25;
        }
        &:before {
            content: '';
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 9;
        }
        &:after {
            background: {
                image: url(data:image/svg+xml;base64,);
                position: center;
                size: cover;
            }
            content: '';
            height: 64px;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 64px;
        }
    }
</style>


