<template>
  <div class="container pt-5">
  <div class="row">
      <div class="col-md-8 col-sm-12 offset-md-2 mb-5">
        <h4 class="mb-3">Verifikasi Pembayaran</h4>

          <div class="row">
            <div class="col-md-12 mb-3">
              <label for="address">Nama</label>
              <input type="text" class="form-control" id="nama" placeholder="nama" required>
              <div class="invalid-feedback">Required.</div>
            </div>

            <div class="col-md-12 mb-3">
              <label for="address2">Jumlah Pembayaran</label>
              <input type="text" class="form-control" id="jumlahbayar" placeholder="Jumlah Pembayaran">
              <div class="invalid-feedback">Required.</div>
            </div>

            <div class="col-md-12 mb-3">
              <label for="address2">Bank Tujuan</label>
              <input type="text" class="form-control" id="banktujuan" placeholder="Bank tujuan">
              <div class="invalid-feedback">Required.</div>
            </div>
            <div class="col-md-5 mb-3">
              <label for="address2">Foto Bukti transaksi</label>
              <input type="file" class="form-control">
              <div class="invalid-feedback">Required.</div>
            </div>
          </div>
          <hr class="mb-4">
          <button class="btn btn-primary btn-lg btn-block" type="submit">Konfirmasi</button>
        </form>
      </div>
    </div>
</div>
</template>

<script>
export default {};
</script>

<style lang="css">
</style>



