<template>
  <div class="hello">
    <Header :menus="menus"></Header>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from '../_layout/headerMahasiswa';

export default {
  components: {
    Header,
  },
  data() {
    return {
      menus: [
          { name: 'Beranda', url: '/mhs' },
          { name: 'Daftar Ujian', url: '/mhs/package' },
          { name: 'Keluar', url: '/logout' },
      ],
    };
  },
};
</script>



