<template>
  <div class="container pt-3">
    <div class="col-md-10 offset-md-1">
      <div class="row">
        <!-- Card Last Achivement -->
        <CardAchivement></CardAchivement>

        <!-- Card Biodata -->
        <CardBiodata></CardBiodata>

        <!-- Link Menu Dashboard -->
        <div class="col-md-6">
          <div class="row">
            <CardLink :link="linkUjian"></CardLink>
            <CardLink :link="linkVerifikasi"></CardLink>
            <CardLink :link="linkDaftar"></CardLink>
            <CardLink :link="linkJadwal"></CardLink>
          </div>
        </div>

        <!-- Card Announcement -->
        <CardAnnoucement></CardAnnoucement>

      </div>
    </div>
  </div>
</template>

<script>
  import CardLink from './_cardLink';
  import CardAchivement from './_cardAchivement';
  import CardBiodata from './_cardBiodata';
  import CardAnnoucement from './_cardAnnoucement';

  export default {
    components: {
      CardLink,
      CardAchivement,
      CardBiodata,
      CardAnnoucement,
    },
    data() {
      return {
        linkUjian: {
          name: 'List Ujian Terakhir',
          url: '/mhs/last-exam',
        },
        linkVerifikasi: {
          name: 'Verifikasi Pembayaran',
          url: '/mhs/verify-payment',
        },
        linkDaftar: {
          name: 'Daftar UKOM',
          url: '/mhs/register',
        },
        linkJadwal: {
          name: 'List Jadwal Ukom',
          url: '/mhs/list-schedule',
        },
      };
    },
  };
</script>

<style>
.img-responsive .img-center {
  margin: 0 auto;
}
.you-get{
  padding-bottom: 10px;
}
.achivement .biodata{
  max-height: 50px !important;
  overflow: hidden;
}
</style>



