<template>
  <nav class="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 mb-3">
    <a href="/" class="navbar-brand col-sm-3 col-md-2 mr-0">
        <img src="../../assets/ukom-logo-white.png" alt="">
    </a>
    <button 
            @click="show = !show"
            class="navbar-toggler" 
            type="button"
            data-toggle="collapse" 
            data-target="#navbarHeader" 
            aria-controls="navbarHeader" 
            aria-expanded="false" 
            aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
    </button>
    <ul class="navbar-nav px-3">
      <li class="nav-item text-nowrap">
        <a class="nav-link" href="#">Keluar</a>
      </li>
    </ul>
    <transition mode="out-in">
        <div v-if="show" class="" id="navbarHeader">
            <div class="container">
                <div class="col-sm-12 py-4 nav nav-masthead justify-content-center">
                    <router-link :to="menu.url" class="nav-link" v-for="menu in menus" :key="menu.id">{{menu.name}}</router-link>
                </div>
            </div>
        </div>
    </transition>
  </nav>
</template>

<script>
  export default {
    props: {
      menus: {
        type: Array,
        required: true,
        default: [
            { name: 'Beranda', url: '/' },
            { name: 'Daftar', url: '/register' },
            { name: 'Masuk', url: '/login' },
            { name: 'Kontak', url: '/contact' },
        ],
      },
    },
    data() {
      return {
        show: false,
      };
    },
  };
</script>

<style scoped>
/*
 * Navbar
 */

.navbar-brand {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, .25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
}

.navbar .form-control {
  padding: .75rem 1rem;
  border-width: 0;
  border-radius: 0;
}

.form-control-dark {
  color: #fff;
  background-color: rgba(255, 255, 255, .1);
  border-color: rgba(255, 255, 255, .1);
}

.form-control-dark:focus {
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, .25);
}
</style>


