<template>
  <div class="navigation">
      <button class="fdk-btn fdk-btn-green fdk-left-item" @click.prevent="prevQuestion()">« Sebelumnya</button>
      <button class="fdk-btn fdk-btn-green fdk-right-item" @click.prevent="nextQuestion()">Berikutnya »</button>
  </div>
</template>

<script>
  export default {
    props: {
      next: {
        type: Number,
        required: true,
      },
      prev: {
        type: Number,
        required: true,
      },
    },
    methods: {
      prevQuestion() {
        this.$soalBus.$emit('questionNumber', this.prev);
      },
      nextQuestion() {
        this.$soalBus.$emit('questionNumber', this.next);
      },
    },
  };
</script>



