<template>
  <nav class="col-md-2 d-none d-md-block bg-light sidebar mt-5 pt-3">
    <div class="sidebar-sticky">
      <ul class="nav flex-column">
        <li class="nav-item" v-for="menu in menus" :key="menu.id">
          <router-link :to="menu.url" class="nav-link">
            <span data-feather="home"></span>
            {{menu.name}}
          </router-link>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
  export default {
    props: {
      menus: {
        type: Array,
        required: true,
        default: [
            { name: 'Beranda', url: '/' },
            { name: 'Daftar', url: '/register' },
            { name: 'Masuk', url: '/login' },
            { name: 'Kontak', url: '/contact' },
        ],
      },
    },
  };
</script>

<style scoped>
/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 0;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.sidebar-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 48px; /* Height of navbar */
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active .router-link-active{
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather .router-link-active {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}


</style>


