/* eslint-disable*/

import Adapter from '@/helpers/Adapter';

export default {
  isMahasiswa() {
    return Adapter.getRole() == 'member';
  },
  isDosen() {

  },
  isAdmin() {
    return Adapter.getRole() == 'administration';
  },
  isReviewwer() {

  },
  isLoggedIn() {
    const jwt = localStorage.getItem('api_token');
    if (jwt != null) {
      return true;
    } else {
      return false;
    }
  },
};



