<template>
<div class="container pt-5">
  <div class="row">
    <div class="col-md-8 col-sm-12 offset-md-2 mb-5">
      <h4 class="mb-3">Daftar UKOM Stikes FDK</h4>
      <form class="needs-validation" novalidate>
        <div v-if="success" class="alert alert-success registered">
          Register berhasil silahkan cek email untuk verifikasi. 
        </div>
        <div class="row">
          <div class="col-md-6 mb-3">
            <label for="firstName">Nama Depan</label>
            <input v-validate:name="'required|alpha_spaces'" v-model="namaDepan" type="text" class="form-control" id="firstName" placeholder="Nama Depan" value="" required>
            <div v-if="errors.firstName" class="error-validation">
              {{errors.firstName[0]}}
            </div>
          </div>
          <div class="col-md-6 mb-3">
            <label for="lastName">Nama Belakang</label>
            <input type="text" class="form-control" id="lastName" v-model="namaBelakang" placeholder="Nama Belakang" value="" required>
            <div v-if="errors.lastName" class="error-validation">
              {{errors.lastName[0]}}
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-md-4 mb-3">
            <label for="country">NIM</label>
            <input type="text" class="form-control" id="nim" placeholder="NIM" v-model="nim" required>
            <div v-if="errors.cardNumber" class="error-validation">
              {{errors.cardNumber[0]}}
            </div>
          </div>
          <div class="col-md-5 mb-3">
            <label for="country">Jurusan</label>
            <select class="custom-select d-block w-100" required v-model="namaJurusan">
                <option :value="jurusan.id" v-for="jurusan in jurusans" :key="jurusan.id">{{jurusan.name}}</option>
              </select>
            <div v-if="errors.majorFieldId" class="error-validation">
              {{errors.majorFieldId[0]}}
            </div>
          </div>
          <div class="col-md-3 mb-3">
            <label for="country">Tahun Angkatan</label>
            <input type="text" class="form-control" id="kelas" placeholder="Tahun Angkatan" v-model="kelas" required>
            <div v-if="errors.grade" class="error-validation">
              {{errors.grade[0]}}
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12 mb-3">
            <label for="firstName">Email</label>
            <input type="text" class="form-control" id="email" placeholder="Email" v-model="email" required>
            <div v-if="errors.email" class="error-validation">
              {{errors.email[0]}}
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-md-6 mb-3">
            <label for="firstName">Password</label>
            <input type="password" class="form-control" id="password" v-model="password" placeholder="******" value="" required>
            <div v-if="errors.password" class="error-validation">
              {{errors.password[0]}}
            </div>
          </div>
          <div class="col-md-6 mb-3">
            <label for="lastName">Konfirmasi Password</label>
            <input type="password" class="form-control" id="konfPassword" v-model="konfirmPassword" placeholder="******" value="" required>
            <div v-if="errors.password_confirmation" class="error-validation">
              {{errors.password_confirmation[0]}}
            </div>
          </div>
        </div>
        <hr class="mb-4">
        <button class="btn btn-primary btn-lg btn-block" 
                @click="postRegister">Daftar</button>
      </form>
    </div>
  </div>
</div>
</template>

<script>
import ApiEndpoint from '@/helpers/Endpoint';

export default {
  data() {
    return {
      jurusans: [],
      errors: [],
      output: [],
      namaDepan: '',
      namaBelakang: '',
      nim: '',
      password: '',
      konfirmPassword: '',
      email: '',
      nama: '',
      namaJurusan: '',
      kelas: '',
      success: false,
    };
  },
  methods: {
    getJurusan() {
      ApiEndpoint.getEndpoint('/select/major/list')
        .then((response) => {
          this.jurusans = response.data.data;
        })
      .catch((e) => {
        this.errors.push(e);
      });
    },
    postRegister(e) {
      e.preventDefault();
      const data = {
        firstName: this.namaDepan,
        lastName: this.namaBelakang,
        email: this.email,
        majorFieldId: this.namaJurusan,
        password: this.password,
        password_confirmation: this.konfirmPassword,
        cardNumber: this.nim,
        grade: this.kelas,
      };
      ApiEndpoint.postEndpoint('/register', data).then((response) => {
        this.output = response.data;
        this.success = true;
      }).catch((error) => {
        this.errors = error.response.data.errors;
      });
    },
  },
  created() {
    this.getJurusan();
  },
};
</script>

<style scoped>

</style>



