/* eslint-disable */
import axios from 'axios';

export default {
  getEndpoint(target, apiToken) {
    let tokenRequest = 'asdasd';

    if (apiToken != null) {
      tokenRequest = apiToken;
    }
    return axios({
      url: 'http://ukom.cimangko.com/api'+target,
      method: 'get',
      headers: { 
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET, POST, PUT, DELETE, PATCH, OPTIONS",
        "Access-Control-Allow-Headers": "X-Requested-With, content-type, Authorization"
      },
      responseType: 'json',
    });
  },
  postEndpoint(target, data, apiToken) {
    let tokenRequest = '';

    if (apiToken != null) {
      tokenRequest = apiToken;
    }
    return axios({
      method: 'post',
      url: 'http://ukom.cimangko.com/api'+target,
      headers: { 
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET, POST, PUT, DELETE, PATCH, OPTIONS",
        "Access-Control-Allow-Headers": "X-Requested-With, content-type, Authorization"
      },
      data: data,
    });
  },
  putEndpoint() {

  },
};



