import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store ({
  state: {
    api_token: '',
    name: '',
    role: '',
    accountId: '',
  },
  mutations: {
    updateToken(state, token) {
      state.api_token = token;
    },
    updateName(state, name) {
      state.name = name;
    },
    updateRole(state, role) {
      state.role = role;
    },
    updateAccountId(state, accountId) {
      state.accountId = accountId;
    },
  },
});



