import Auth from '@/helpers/Middleware';
import MasterMahasiswa from '@/components/Mahasiswa/MasterMahasiswa';
import MahasiswaBeranda from '@/components/Mahasiswa/Dashboard/Index';
import MahasiswaPaketUjian from '@/components/Mahasiswa/Register/Package';
import MahasiswaDaftarUjian from '@/components/Mahasiswa/Register/Index';
import MahasiswaUjian from '@/components/Mahasiswa/Exam/Index';
import MahasiswaVerifyPayment from '@/components/Mahasiswa/Payment/Index';
// import MahasiswaIntro from '@/components/Mahasiswa/Exam/Intro';
import MahasiswaJadwal from '@/components/Mahasiswa/Schedule/Index';
import Logout from '@/components/Logout';

export default {
  path: '/mhs',
  name: 'Mahasiswa',
  meta: {
    requiresAuth: true,
  },
  redirect: '/dashboard',
  component: MasterMahasiswa,
  children: [
    {
      path: '/mhs',
      component: MahasiswaBeranda,
    },
    {
      path: '/mhs/package',
      component: MahasiswaPaketUjian,
    },
    {
      path: '/mhs/register',
      component: MahasiswaDaftarUjian,
    },
    {
      path: '/mhs/ujian/:number',
      component: MahasiswaUjian,
    },
    // {
    //   path: '/mhs/intro',
    //   component: MahasiswaIntro,
    // },
    {
      path: '/mhs/list-schedule',
      component: MahasiswaJadwal,
    },
    {
      path: '/mhs/verify-payment',
      component: MahasiswaVerifyPayment,
    },
    {
      path: '/logout',
      component: Logout,
    },
  ],
  beforeEnter: (to, from, next) => {
    if (!Auth.isMahasiswa()) {
      next({
        path: '/login',
      });
    } else {
      next();
    }
  },
};



