// admin
import Auth from '@/helpers/Middleware';
import AdminMaster from '@/components/Admin/MasterAdmin';
import AdminHome from '@/components/Admin/Index';

export default {
  path: '/admin',
  name: 'Admin',
  meta: {
    requiresAuth: true,
  },
  redirect: '/dashboard',
  component: AdminMaster,
  children: [
    {
      path: '/',
      component: AdminHome,
    },
  ],
  beforeEnter: (to, from, next) => {
    if (!Auth.isAdmin()) {
      next({
        path: '/login',
      });
    } else {
      next();
    }
  },
};



