<template>
  <div class="hello">
    <Header :menus="menus"></Header>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from '../_layout/headerMahasiswa';

export default {
  components: {
    Header,
  },
  data() {
    return {
      menus: [
          { name: 'Beranda', url: '/' },
          { name: 'Daftar', url: '/register' },
          { name: 'Masuk', url: '/login' },
          { name: 'Kontak', url: '/contact' },
      ],
    };
  },
};
</script>



