<template>
  <div class="card mb-4 box-shadow">
    <div class="card-header">
      <h4 class="my-0 font-weight-normal">{{paket.name}}</h4>
    </div>
    <div class="card-body">
      <h2 class="card-title pricing-card-title">{{paket.price}}</h2>
      <ul class="list-unstyled mt-3 mb-4">
        <li v-for="detail in paket.detail">{{detail}}</li>
        <li v-if="paket.trialTahunan">
          <div class="form-label-group mt-5">
            <input type="text" id="paket-kode" class="form-control" placeholder="Password" required>
            <label for="paket-kode">Masukkan Kode Paket</label>
          </div>
        </li>
      </ul>
      <a :href="paket.btnUrl" class="btn btn-lg btn-block btn-outline-secondary">{{paket.btnName}}</a>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      paket: {
        type: Array,
        required: true,
        default: {
          name: 'Trayout',
          price: 'Gratis',
          detail: [
          ],
          btnName: 'Mulai Ujian',
          btnUrl: '/',
          trialTahunan: false,
        },
      },
    },
  };
</script>

<style scoped>

</style>


